.nosotros__iconos {
    @include tablet {
        @include grid(3, 2rem);
    }
}
.nosotros__icono {
    margin-bottom: 3rem;
    background-color: lighten($gris, 5);
    padding: 2rem;
    
    text-align: center;
    img {
        height: 10rem;
    }
    h3 {
        text-transform: uppercase;
        margin: 0;
    }
    p {
        font-size: 1.6rem;
    }
}