.cards__anuncios {
    @include tablet {
        @include grid(3, 2rem);
    }

    .cards__anuncio {
        background-color: lighten($gris, 5);
        border: 1px solid darken($gris, 10);
        margin-bottom: 2rem;
        @include tablet {
            margin-bottom: 0;
        }
        h3,
        p {
            margin: 0;
        }
    }

    .cards__contenido {
        padding: 2rem;
    }

    .cards__texto {
        font-size: 1.8rem;
    }

    .cards__precio {
        color: $verde;
        font-weight: $bold;
        font-size: 3rem;
    }

    .cards__lista {
        display: flex;
        list-style: none;
        padding: 0;

        li {
            flex: 1;
            display: flex;
            align-items: center;
            img {
                flex: 0 0 5rem;
                margin-right: 1rem;
            }

            p {
                font-weight: $black;
            }
        }
    }
}

.cards__alinear {
    display: flex;
    justify-content: flex-end;
}