.nosotros__content {
    @include tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 2rem;
    }
}
.nosotros__texto {
    blockquote {
        font-weight: $black;
        font-size: 2rem;
        margin: 2rem 0;
        padding-left: 1rem 0 3rem 0;
        color: $amarillo;
    }
}