.anuncio__precio {
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}
.anuncio__lista {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    li {
        flex: 1;
        display: flex;
        align-items: center;
        img {
            flex: 0 0 5rem;
            margin-right: 1rem;
        }

        p {
            font-weight: $black;
        }
    }
}

