.blog__info {
    @include tablet {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 3rem;
    }
}

.blog__entrada {
    margin-bottom: 5rem;
    border-bottom: 1px solid $verde;
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
        margin-bottom: 3rem;
        max-width: 70rem;
    }
    a {
        color: $negro;
        margin-top: 2rem;
        display: block;
        @include tablet {
            margin-top: 0;
        }
    }

    h4 {
        margin: 0;
        line-height: 1.4;
        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }

    
}

.blog__texto {
    p {
        text-align: justify;
        font-size: 1.6rem;
    }
}

.blog__meta {
    span {
        color: $verde;
    }
}