.header__barra {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
}

.header__nav {
    display: none;
    @include tablet {
        display: flex;
        flex-direction: row;
    }

    a {
        color: $blanco;
        font-size: 1.7rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
            font-size: 2rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
        &:hover {
            color: $amarillo;
            cursor: pointer;
        }
    }
    img {
        width: 4rem;
        margin-left: 2rem;
        cursor: pointer;
    }
}

/** Menu de hamburguesa **/
.mobile__menu {
    padding-top: 1rem;
    @include tablet {
        display: none;
    }

    img {
        width: 3rem;
        cursor: pointer;
    }
}

.mostrar__nav {
    visibility: visible;
    opacity: 1;
    height: auto;
    display: block;
}

