.testimoniales__content {
  background-color: $amarillo;
  font-size: 2rem;
  padding: 1.3rem;
  color: $blanco;
  border-radius: 0.5rem;
  blockquote {
    text-align: justify;
    position: relative;
    font-size: 1.6rem;
    &::before {
      content: "";
      background-image: url(../img/comilla.svg);
      background-repeat: no-repeat;
      width: 4rem;
      height: 8rem;
      display: block;
      position: absolute;
      left: -4rem;
      top: -1rem;
    }
  }
  p {
    color: $negro;
    text-align: right;
    font-size: 1.6rem;
    font-weight: $bold;
  }
}
