.dark-mode {
    background-color: darken($grisOscuro, 10);
    p{
        color: $blanco;
    }
    a,h1,h2,h3,h4 {
        color: $gris;
    }
    /* anuncios */
    .cards__contenido {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
    }
    .nosotros__icono {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
    }
    .cards__icono {
        filter: invert(100%);
    }
    /* anuncio entrada */
    .anuncio__precio {
        color: $amarillo;
    }
    /* nosotros */
    blockquote {
        color: $blanco;
    }
    /* contacto */
    .formulario {
        legend {
            color: $verde;
        }
        label {
            color: $blanco;
        }
        p {
            color: $amarillo;
        }
        input:not([type="submit"]), textarea, select {
            background-color: $grisOscuro;
            border: 1px solid $gris;
            color: $gris;
        }
    }
}