/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin boton($bg,$color, $display){
    background-color: $bg;
    font-weight: $bold;
    text-decoration: none;
    padding: 1rem 3rem;
    border: none;
    cursor: pointer;
    color: $color;
    text-align: center;
    margin-top: calc($separacion / 2);
    display: $display;
    &:hover {
        background-color: darken($bg, 5);
    }
}