.no-webp .contacto {
    background-image: url(../img/encuentra.jpg);
}
.webp .contacto {
    background-image: url(../img/encuentra.webp);
}
.contacto {
    background-position: center center;
    background-size: cover;
    padding: $separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2 {
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;
    }
    p {
        color: $blanco;
        text-align: center;
    }
}