.footer {
    background-color: $grisOscuro;
    padding: 1rem 0;
    margin-bottom: 0;

    &__contenido {
        display: flex;
        flex-direction: column;
        @include tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__nav {
        padding-top: 2rem;
        display: flex;
        flex-direction: column;
        @include tablet {
            flex-direction: row;
            padding-top: 0;
        }
        a {
            color: $blanco;
            font-size: 1.7rem;
            text-align: center;
            @include tablet {
                flex-direction: row;
                margin-right: 2rem;
                font-size: 2rem;
                &:last-of-type {
                    margin-right: 0;
                }
            }
            &:hover {
                color: $verde;
            }
        }
    }

    &__copyright {
        color: $blanco;
        text-align: center;
    }
}