/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 1.8rem;
  text-align: justify;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.btn__amarillo {
  background-color: #E08709;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  border: none;
  cursor: pointer;
  color: #FFFFFF;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}
.btn__amarillo:hover {
  background-color: #c77808;
}

.btn__amarillo--block {
  background-color: #E08709;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  border: none;
  cursor: pointer;
  color: #FFFFFF;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}
.btn__amarillo--block:hover {
  background-color: #c77808;
}

.btn__verde {
  background-color: #71B100;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  border: none;
  cursor: pointer;
  color: #FFFFFF;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}
.btn__verde:hover {
  background-color: #619800;
}

.dark-mode {
  background-color: #1a1a1a;
  /* anuncios */
  /* anuncio entrada */
  /* nosotros */
  /* contacto */
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .cards__contenido {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .nosotros__icono {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .cards__icono {
  filter: invert(100%);
}
.dark-mode .anuncio__precio {
  color: #E08709;
}
.dark-mode blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario legend {
  color: #71B100;
}
.dark-mode .formulario label {
  color: #FFFFFF;
}
.dark-mode .formulario p {
  color: #E08709;
}
.dark-mode .formulario input:not([type=submit]), .dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #e1e1e1;
  color: #e1e1e1;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 2rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header.inicio .header__contenido {
    min-height: 70rem;
  }
}
.header__name {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #333333;
  padding: 1rem 0;
  margin-bottom: 0;
}
.footer__contenido {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.footer__nav {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer__nav {
    flex-direction: row;
    padding-top: 0;
  }
}
.footer__nav a {
  color: #FFFFFF;
  font-size: 1.7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__nav a {
    flex-direction: row;
    margin-right: 2rem;
    font-size: 2rem;
  }
  .footer__nav a:last-of-type {
    margin-right: 0;
  }
}
.footer__nav a:hover {
  color: #71B100;
}
.footer__copyright {
  color: #FFFFFF;
  text-align: center;
}

.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header__nav {
  display: none;
}
@media (min-width: 768px) {
  .header__nav {
    display: flex;
    flex-direction: row;
  }
}
.header__nav a {
  color: #FFFFFF;
  font-size: 1.7rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .header__nav a {
    margin-right: 2rem;
    font-size: 2rem;
  }
  .header__nav a:last-of-type {
    margin-right: 0;
  }
}
.header__nav a:hover {
  color: #E08709;
  cursor: pointer;
}
.header__nav img {
  width: 4rem;
  margin-left: 2rem;
  cursor: pointer;
}

/** Menu de hamburguesa **/
.mobile__menu {
  padding-top: 1rem;
}
@media (min-width: 768px) {
  .mobile__menu {
    display: none;
  }
}
.mobile__menu img {
  width: 3rem;
  cursor: pointer;
}

.mostrar__nav {
  visibility: visible;
  opacity: 1;
  height: auto;
  display: block;
}

@media (min-width: 768px) {
  .nosotros__iconos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.nosotros__icono {
  margin-bottom: 3rem;
  background-color: #eeeeee;
  padding: 2rem;
  text-align: center;
}
.nosotros__icono img {
  height: 10rem;
}
.nosotros__icono h3 {
  text-transform: uppercase;
  margin: 0;
}
.nosotros__icono p {
  font-size: 1.6rem;
}

@media (min-width: 768px) {
  .cards__anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.cards__anuncios .cards__anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .cards__anuncios .cards__anuncio {
    margin-bottom: 0;
  }
}
.cards__anuncios .cards__anuncio h3,
.cards__anuncios .cards__anuncio p {
  margin: 0;
}
.cards__anuncios .cards__contenido {
  padding: 2rem;
}
.cards__anuncios .cards__texto {
  font-size: 1.8rem;
}
.cards__anuncios .cards__precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}
.cards__anuncios .cards__lista {
  display: flex;
  list-style: none;
  padding: 0;
}
.cards__anuncios .cards__lista li {
  flex: 1;
  display: flex;
  align-items: center;
}
.cards__anuncios .cards__lista li img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.cards__anuncios .cards__lista li p {
  font-weight: 900;
}

.cards__alinear {
  display: flex;
  justify-content: flex-end;
}

.no-webp .contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .contacto {
  background-image: url(../img/encuentra.webp);
}

.contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.contacto p {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .blog__info {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 3rem;
  }
}

.blog__entrada {
  margin-bottom: 5rem;
  border-bottom: 1px solid #71B100;
}
@media (min-width: 768px) {
  .blog__entrada {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    margin-bottom: 3rem;
    max-width: 70rem;
  }
}
.blog__entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .blog__entrada a {
    margin-top: 0;
  }
}
.blog__entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.blog__entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.blog__texto p {
  text-align: justify;
  font-size: 1.6rem;
}

.blog__meta span {
  color: #71B100;
}

.testimoniales__content {
  background-color: #E08709;
  font-size: 2rem;
  padding: 1.3rem;
  color: #FFFFFF;
  border-radius: 0.5rem;
}
.testimoniales__content blockquote {
  text-align: justify;
  position: relative;
  font-size: 1.6rem;
}
.testimoniales__content blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 4rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -4rem;
  top: -1rem;
}
.testimoniales__content p {
  color: #000000;
  text-align: right;
  font-size: 1.6rem;
  font-weight: 700;
}

@media (min-width: 768px) {
  .nosotros__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}

.nosotros__texto blockquote {
  font-weight: 900;
  font-size: 2rem;
  margin: 2rem 0;
  padding-left: 1rem 0 3rem 0;
  color: #E08709;
}

.anuncio__precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.anuncio__lista {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.anuncio__lista li {
  flex: 1;
  display: flex;
  align-items: center;
}
.anuncio__lista li img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.anuncio__lista li p {
  font-weight: 900;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 2rem;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  display: block;
  text-transform: uppercase;
}
.formulario label span {
  color: #E08709;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
  resize: none;
}
.formulario__contacto {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario__btn {
  width: 100%;
  padding: 1.3rem;
}
@media (min-width: 768px) {
  .formulario__btn {
    width: auto;
  }
}